@use '../../base/01-base' as base;

.eui-showcase-demo {
    position: relative;

    &-flex {
        align-items: center;
        display: flex;
        width: 100%;
    }

    &-flex-column {
        align-self: flex-start;
        flex: 1 1 50%;
        width: 50%;
    }
}

.eui-showcase-demo,
.eui-showcase-demo-flex,
.eui-showcase-demo-flex-column {
    .flex-spacer {
        flex: 1 1 auto;
        margin: 0 2rem;
    }

    .code {
        background-color: var(--eui-c-s-secondary-surface-light);
        border: 2px dotted silver;
        display: block;
        height: 100vh;
        max-height: 20rem;
        overflow-y: auto;
        padding: 0.5rem;
        position: relative;
        width: 100%;
        @include base.eui-scrollbars(6px);

        pre {
            color: var(--eui-c-s-danger);
            display: block;
            font-family: monospace;
            font-size: 95%;
            margin-bottom: 0;
            margin-top: 0;
            overflow: initial;
            white-space: pre;
            word-wrap: break-word;

            > * {
                align-items: center;
                display: flex;
                font-size: 100%;
            }
        }

        pre.newline {
            display: flex;
            margin-bottom: 0;
        }
        pre.inline {
            display: inline-flex;
        }

        .one {
            margin-left: 2rem;
        }

        .two {
            margin-left: 4rem;
        }

        .three {
            margin-left: 6rem;
        }

        .four {
            margin-left: 8rem;
        }

        .five {
            margin-left: 10rem;
        }

        .vspace {
            margin-bottom: 0.5rem !important;
        }
    }
}
