@forward '../../base/03-vars/vars-legacy-hc-theme';

html.eui-t-eui-legacy.eui-t-high-contrast {
    --eui-c-neutral: var(--eui-c-black);
    --eui-c-text: var(--eui-c-black);

    // generic utilities
    .eui-u-text-code {
        font-weight: 600 !important;
    }

    // components based
    .eui-search {
        .eui-input-text {
            background-color: var(--eui-c-white);
        }
        .eui-search__icon {
            color: var(--eui-c-black) !important;
        }
    }

    .eui-header-environment {
        opacity: 1;
        background-color: var(--eui-c-s-danger-surface);
    }

    .eui-toolbar-environment {
        background-color: var(--eui-c-s-danger-surface);
    }
}
