@use '../../base/02-tokens/maps-ds2025-theme' as ds2025;

// BG-COLORS
@each $color, $shades in default.$color-map {
    @each $shade, $value in $shades {
        .eui-showcase-bg-color-#{$color + '-' + $shade}::after {
            content: "#{$value}";
        }
    }
}

