@each $size, $value in default.$spacing-map {
    .eui-showcase-spacing-size-#{$size}::after {
        content: "#{$value}";
    }
}

.eui-showcase-shadows {
    td {
        display: table-cell;
        vertical-align: middle !important;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--eui-c-white);
        width: var(--eui-s-2xl);
        height: var(--eui-s-2xl);
        margin: var(--eui-s-xl);
    }
}

.eui-showcase-download {
    display: inline-flex;
    align-items: center;
    padding: var(--eui-s-m);
    border: 1px solid var(--eui-c-divider);
    background-color: var(--eui-c-white);

    &__icon {
        width: var(--eui-s-xl);
        margin-right: var(--eui-spacing-s);
    }

    &__title {
        font: var(--eui-f-m);
        font-weight: bold;
    }

    &__subtitle {
        font: var(--eui-f-s);
        color: var(--eui-c-text-light);
        margin-top: var(--eui-spacing-2xs);
    }
}


// generating contrast ratio based on the default maps of colors known

@each $color, $shades in default.$color-map {
    @each $shade, $value in $shades {
        // @debug $color, $value, find-safe-color($value);
        $contrastRatio: base.calculate-contrast($value, base.getContrast($value));

        .eui-showcase-ratio-#{$color + '-' + $shade}::after {
            content: "#{$contrastRatio}";
        }
        .eui-showcase-ratio-#{$color + '-' + $shade} {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 0.75rem;
            padding: 0.0375rem;
            min-width: 1.5rem;
        }

        @if $contrastRatio < 4.5 {
            .eui-showcase-ratio-#{$color + '-' + $shade} {
                background-color: var(--eui-c-s-danger);
                color: var(--eui-c-white);
            }
        } @else {
            .eui-showcase-ratio-#{$color + '-' + $shade} {
                background-color: var(--eui-c-s-success);
                color: var(--eui-c-white);
            }
        }


    }
}

// special utilities showcase only for themes
