// HIGH CONTRAST

:root {
    @each $color, $shades in legacyHc.$color-map {
        @each $shade, $value in $shades {
            --eui-bc-hc-#{$color + '-' + $shade}: #{$value};
            // --eui-bc-hc-#{$color + '-' + $shade}-contrast: #{base.recommend-text-color($value)};
            --eui-bc-hc-#{$color + '-' + $shade}-contrast: #{base.getContrast($value)};
        }
    }
}


