@use '../../base/01-base' as base;

code[class*="language-"],
pre[class*="language-"] {
    color: black;
    background: none;
    text-shadow: none;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;

    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;

    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;

    font-size: 14px;
    @include base.eui-accessible-focus();
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
    text-shadow: none;
    background: #f4f4f4;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
    text-shadow: none;
    background: #b3d4fc;
}

@media print {
    code[class*="language-"],
    pre[class*="language-"] {
        text-shadow: none;
    }
}

/* Code blocks */
/* Code blocks */
pre[class*="language-"] {
   position: relative;
   margin: .5em 0;
   overflow: visible;
   padding: 0;
}
pre[class*="language-"]>code {
   position: relative;
   border-left: 4px solid var(--eui-c-s-info-base);
   background-color: #fdfdfd;
   background-size: 4em 3em;
   background-origin: content-box;
   background-attachment: local;
   padding: 0.5em;
}

code[class*="language"] {
   max-height: inherit;
   height: inherit;
   padding: 0 1em;
   display: block;
   overflow: auto;
   font-size: var(--eui-f-size-base);
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
    background: #fff;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
    padding: 0.5em;
    border-radius: .3em;
    white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color: slategray;
}

.token.punctuation {
    color: #5f5f5f;
}

.token.namespace {
    opacity: .7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
    color: var(--eui-c-s-danger);
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
    color: var(--eui-c-s-success);
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
    color: #5f3400;
    background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
    color: var(--eui-c-s-info);
}

.token.function,
.token.class-name {
    color: var(--eui-c-s-danger);
}

.token.regex,
.token.important,
.token.variable {
    color: var(--eui-c-s-warning);
}

.token.important,
.token.bold {
    font-weight: bold;
}
.token.italic {
    font-style: italic;
}

.token.entity {
    cursor: help;
}
